  --background-color: #0e180e;
  --background-color-lose: #250000;
  --text-color: #33ff33;
  --gameboard-bg-color: #0f1b0f;
  --gameboard-border-color: #004d00;
  --board-border-color: #003d00;
  --board-bg-color: #0d190d;
