  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: url(${___CSS_LOADER_URL_REPLACEMENT_1___}) center no-repeat;
  background-size: 100% 100%;
  pointer-events: none;
  z-index: 9999;
}

@keyframes scanlines {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 0 4px;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    transform: scale(0.9);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

#menu-ascii {
  font-family: "OCRAEXT";
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 1.3rem;
  color: var(--text-color);
}

#menu-ascii > * {
  margin: 0;
}

#menu-buttons {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  margin-bottom: 2rem;
}

#menu-buttons button {
  font-family: "OCRAEXT", Impact;
  background-color: var(--board-bg-color);
  color: var(--text-color);
  border: none;
  border: 0.25rem solid var(--board-border-color);
  border-radius: 0.6rem;
  padding: 0.6rem 1.2rem;
  font-size: 1.6rem;
  cursor: pointer;
  transition: transform 0.2s ease, background-color 0.2s ease;
}

#menu-buttons button:hover {
  background-color: var(--cell-highlight-color);
  transform: scale(1.1);
}

#gameboards {
  display: none;
  gap: 1.5rem;
  padding: 1.5rem;
  background-color: var(--gameboard-bg-color);
  border-radius: 0.8rem;
  border: 0.25rem solid var(--gameboard-border-color);
  position: relative;
  image-rendering: pixelated;
  align-items: center;
  justify-content: center;
  transition: transform 0.2s ease;
}

.board-container {
  display: none;
  flex-direction: column;
  gap: 0.8rem;
  align-items: center;
}

.board-label {
  font-size: 1.3rem;
  font-weight: bold;
  color: var(--text-color);
}

.board {
  display: grid;
  grid-template: repeat(10, 1fr) / repeat(10, 1fr);
  gap: 0.2rem;
  box-sizing: border-box;
  border: 0.25rem solid var(--board-border-color);
  padding: 0.25rem;
  background-color: var(--board-bg-color);
  border-radius: 0.4rem;
  transform: rotate(-90deg);
}

.board > * {
  transform: rotate(90deg);
}

.cell {
  padding: 0.2rem;
  width: 2.5rem;
  height: 2.5rem;
  background-color: var(--cell-bg-color);
  border: 0.08rem solid var(--cell-border-color);
  transition: all 0.1s ease;
}

#p2-board .cell {
  pointer-events: auto;
}

#p2-board:hover {
  cursor: url(${___CSS_LOADER_URL_REPLACEMENT_2___}), auto;
}

#p2-board .cell:hover {
  filter: brightness(1.5);
}

.cell.hit {
  background-color: var(--cell-hit-color);
}

.cell.miss {
  background-color: var(--cell-miss-color);
}

.cell.ship {
  background-color: var(--cell-ship-color);
}

.cell.sunk {
  background-color: var(--cell-sunk-color);
}

.board-button {
  background-color: var(--board-bg-color);
  color: var(--text-color);
  border: none;
  border: 0.25rem solid var(--board-border-color);
  border-radius: 0.6rem;
  padding: 0.6rem 1.2rem;
  font-size: 1.6rem;
  cursor: pointer;
  transition: transform 0.2s ease, background-color 0.2s ease;
}

.board-button:hover {
  background-color: var(--cell-highlight-color);
  transform: scale(1.1);
}

.board-button:active {
  transform: scale(0.9);
}

#ship-placement {
  display: flex;
  flex-direction: column;
  width: 17rem;
  height: 35rem;
  border: 0.25rem solid var(--board-border-color);
  border-radius: 0.6rem;
  padding: 1.2rem 0.6rem;
  gap: 0.6rem;
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
}

.ship-placement-header {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
  padding: 0.6rem;
  background-color: var(--board-bg-color);
  border-radius: 0.6rem;
  border: 0.25rem solid var(--board-border-color);
  margin-bottom: 1.2rem;
}

.ship-placement-header .board-label {
  font-size: 1.3rem;
  font-weight: bold;
  color: var(--text-color);
}

.ship-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0.6rem;
  background-color: var(--board-bg-color);
  border-radius: 0.6rem;
  border: 0.25rem solid var(--board-border-color);
}

.ship-label {
  font-size: 1.1rem;
  font-weight: bold;
  color: var(--text-color);
  margin-bottom: 0.6rem;
  text-align: center;
}

.ship-draggable {
  display: flex;
  flex-direction: row;
  gap: 0.2rem;
}

.ship-draggable:hover {
  transform: scale(1.05);
  cursor: move;
}

.ship-draggable:active {
  opacity: 0.2;
  cursor: move;
}

.ship-part {
