  font-family: "OCRAEXT", Impact;
  background-color: var(--board-bg-color);
  color: var(--text-color);
  border: none;
  border: 0.25rem solid var(--board-border-color);
  border-radius: 0.6rem;
  padding: 0.6rem 1.2rem;
  font-size: 1.6rem;
  cursor: pointer;
  transition: transform 0.2s ease, background-color 0.2s ease;
}

#deploy-button:hover {
  background-color: var(--cell-highlight-color);
  transform: scale(1.1);
}

#settings-modal {
  display: none;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 9997;
}

.modal {
  display: none;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.4);
  display: flex;
  justify-content: center;
  align-items: center;
  backdrop-filter: blur(5px);
}

.modal-content {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: var(--board-bg-color);
  border: 0.25rem solid var(--board-border-color);
  border-radius: 0.6rem;
  padding: 2rem;
  width: 80%;
  max-width: 500px;
  box-sizing: border-box;
  text-align: left;
  color: var(--text-color);
  animation: fadeIn 0.5s ease-out;
  margin: auto;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.close-button {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 1.6rem;
  width: 1.6rem;
  color: var(--text-color);
  font-size: 2rem;
  cursor: pointer;
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  background-color: var(--board-bg-color);
  border: 0.25rem solid var(--board-border-color);
  border-radius: 0.6rem;
  transition: transform 0.2s ease, background-color 0.2s ease;
}

.close-button:hover,
.close-button:focus {
  cursor: grab;
  background-color: var(--cell-highlight-color);
  transform: scale(1.1);
}

.close-button:active {
  transform: scale(0.9);
}

.settings-option {
  display: flex;
  align-items: center;
  margin: 1rem 0;
  font-size: 1.4rem;
  color: var(--text-color);
  user-select: none;
}

.settings-option input[type="checkbox"] {
  display: none;
}

.settings-option input[type="checkbox"] + label {
  position: relative;
  padding-left: 2rem;
  cursor: pointer;
  font-size: 1.4rem;
}

.settings-option input[type="checkbox"] + label::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 1.4rem;
  height: 1.4rem;
  border: 2px solid var(--text-color);
  border-radius: 0.3rem;
  background-color: var(--board-bg-color);
  transition: background-color 0.2s ease, border-color 0.2s ease;
}

.settings-option input[type="checkbox"]:checked + label::before {
  background-color: var(--text-color);
  border-color: var(--cell-highlight-color);
}

.settings-option input[type="checkbox"]:checked + label::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 1.6rem;
  height: 1.6rem;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.2rem;
  color: var(--board-bg-color);
}

.settings-option input[type="checkbox"]:hover + label::before {
  background-color: var(--cell-highlight-color);
}

#save-settings-button {
  font-family: "OCRAEXT", Impact;
  background-color: var(--board-bg-color);
  color: var(--text-color);
  border: 0.25rem solid var(--board-border-color);
  border-radius: 0.6rem;
  padding: 0.6rem 1.2rem;
  font-size: 1.6rem;
  cursor: pointer;
  transition: transform 0.2s ease, background-color 0.2s ease;
  margin-top: 1.5rem;
}

#save-settings-button:hover {
  background-color: var(--cell-highlight-color);
  transform: scale(1.1);
}

#save-settings-button:active {
  transform: scale(0.9);
}

#game-over-screen {
  display: none;
  flex-direction: column;
  font-family: "OCRAEXT";
  align-items: center;
  justify-content: center;
}

.game-over-message {
  margin: 0; /* Reset margin */
  padding: 0; /* Reset padding */
  text-align: left; /* Ensure text alignment */
  white-space: pre; /* Preserve whitespace */
  display: none;
  font-family: "OCRAEXT";
  font-size: 1.1rem;
  color: var(--text-color);
}

#game-lose-message {
  color: var(--cell-hit-color);
}

#back-to-menu-button {
  width: fit-content;
  font-family: "OCRAEXT", Impact;
  background-color: var(--board-bg-color);
  color: var(--text-color);
  border: 0.25rem solid var(--board-border-color);
  border-radius: 0.6rem;
  padding: 0.6rem 1.2rem;
  font-size: 1.6rem;
  cursor: pointer;
  transition: transform 0.2s ease, background-color 0.2s ease;
  margin-top: 1.5rem;
}

#back-to-menu-button:hover {
  cursor: pointer;
  background-color: var(--cell-highlight-color);
  transform: scale(1.1);
